
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body {
  background-color: #272525;
  color: #F0F0F0;
  font-size: 14px;
  margin-top: 95px;
  font-family: 'Lato', Arial, Helvetica, sans-serif !important;
  height: unset !important;
}
h1, h2, h3, h4, h5 {
  font-family: 'Lato-Black';
}

#menu {
  z-index: 100;
  position: fixed;
}
.headmenu {
  position: fixed;
  top: 0px;
  padding: 8px;
  background-color: #191717;
  width: 100%;
  cursor: pointer;
}
.menuoverlay {
  background-color: rgba(0, 0, 0, 0.9);
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0px;
  left: 0px;
  z-index: 100;
}
.menubar {
  color: #ffffff;
  transition: all 0.5s;
  background-color: #111010;
  position: fixed;
  top: 0px;
  left: 0px;
  height: 100%;
  width: 200px;
  z-index: 110;
}
.menubar_head {
  background-color: #337ab7;
  padding: 10px;
  font-weight: bold;
}
.menuelement {
  padding: 10px;
  padding-left: 30px;
  border-bottom: solid 1px #171717;
  transition: all 0.5s ease;
}
.menuelement a {
  color: #ffffff;
}
.menuelement:hover {
  background-color: #151515;
}
.menuelement a:hover {
  text-decoration: none;
}
.slide-enter-active,
.slide-leave-active {
  transition: transform 0.3s;
}
.slide-enter,
.slide-leave-to {
  transform: translateX(-100%);
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.3s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}

.pbreadcrumb {
  position: fixed;
  z-index: 80;
  top: 46px;
  background-color: #1d1b1b;
  width: 100%;
  height: 45px;
  padding: 5px;
  padding-left: 10px;
}
.pbreadcrumb .bc_element {
  float: left;
  background-color: rgba(66, 66, 66, 0.35);
  padding: 6px;
  margin-right: 3px;
  border-radius: 2px;
  color: #b5b5b5;
  border: 1px solid #2a2929;
  transition: all 0.5s cubic-bezier(0.4, 0, 0.68, 0.97);
  cursor: pointer;
}
.pbreadcrumb .bc_element:first-child {
  width: 50px;
  text-align: center;
}
.pbreadcrumb .bc_element:hover {
  border: 1px solid rgba(75, 103, 121, 0.77);
}
.items {
  padding: 7px;
}
.btn {
  background-color: #337ab7;
}
.moreImagesBtn {
  margin: 15px auto;
}
.moreImagesContainer {
  clear: both;
  text-align: center;
}
.not_found_center {
  width: 300px;
  margin: 0 auto;
  text-align: center;
}
img.loading {
  background-color: #ffffff;
  padding: 10px;
  border-radius: 3px;
  z-index: 100;
  position: fixed;
  top: 10px;
  right: 10px;
  transition: all 0.3s;
}
.btn {
  color: #ffffff;
  background-color: #337ab7;
  border-color: #1d4d75;
}
.btn:hover {
  color: #ffffff;
  background-color: #2e5f8a;
  border-color: #1d4d75;
}
.directoryOptions {
  position: fixed;
  top: 92px;
  width: 100px;
  right: 5px;
  z-index: 90;
}




































































.item_area {
  position: relative;
  float: left;
}
.item .reloadInfo {
  font-size: 11px;
  padding: 10px;
}
.item {
  position: relative;
  float: left;
  margin: 5px;
  border-radius: 3px;
  background-color: rgb(29, 27, 27);
  width: 150px;
  height: 150px;
  overflow: hidden;
  border: 1px solid #1d1b1b;
  box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.63);
  transition: all 0.5s cubic-bezier(0.4, 0, 0.68, 0.97);
  cursor: pointer;
}
.item:hover {
  border: 1px solid rgba(75, 103, 121, 0.77);
  box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.63);
}
.item:hover > .menu_button {
  transition: display 0.5s;
  display: block;
}
.item_thumb_static {
  padding: 20px 15px;
  margin-left: 0px !important;
  margin-top: 0px !important;
}
.item_name {
  position: absolute;
  left: 0px;
  bottom: 0px;
  padding: 3px 5px;
  background-color: rgba(75, 103, 121, 0.77);
  text-shadow: 0px 0px 5px rgba(43, 43, 43, 0.31);
  border-radius: 0px 3px;
}
.item img {
  max-width: 150px;
  max-width: 225px;
  margin-top: 0px;
  margin-left: -20px;
}
.menu_button:hover {
  color: #ffffff;
  font-size: 25px;
}
.menu_button {
  right: 6px;
  position: absolute;
  z-index: 30;
  font-size: 20px;
  color: #f0f0f0;
  bottom: 5px;
  cursor: pointer;
  padding: 5px;
  transition: display 0.5s, font-size 0.3s;
}
.menu {
  position: absolute;
  background-color: #1d1b1b;
  bottom: -165px;
  right: 10px;
  z-index: 40;
  width: 140px;

  font-size: 14px;
  text-align: left;
  -webkit-background-clip: padding-box;
  background-clip: padding-box;
  border: 2px solid rgba(19, 19, 19, 0.61);
  border-radius: 4px;
  -webkit-box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
}
.menu ul {
  list-style: none;
  padding: 0px;
}
.menu ul li a {
  color: #ffffff;
}
.menu ul li a:hover {
  text-decoration: none;
}
.menu ul li {
  padding: 10px;
  border-bottom: 1px solid rgba(35, 34, 34, 0.55);
  cursor: pointer;
  transition: all 0.3s;
}
.menu ul li i {
  padding-right: 5px;
}
.menu .menu_element:hover {
  background-color: #191717;
}



































































































