#lightbox {
  position: fixed;
  top: 0px;
  z-index: 100;
}
#lightbox #lightbox_window {
  position: fixed;
  top: 10%;
  /*left: 10%*/
  max-width: 80%;
  z-index: 110;

  transition: all 0.3s;
  box-shadow: 0px 0px 15px 0px black;
  border-radius: 3px;
}
#lightbox #lightbox_window img {
  transition: all 0.3s;
  border-radius: 3px;
}
.picture_position {
  position: absolute;
  right: 5px;
  top: -20px;
  opacity: 0.4;
  font-size: 10px;
}
.window_buttons {
  /* fixed on the right side */
  position: fixed;
  top: 10%;
  right: 0px;

  /* Floating with image
    position: absolute;
    top: -16px;
    right: -57px;
    float: left;
    */
  width: 65px;
}
.window_button {
  margin: 6px 10px;
  padding: 15px !important;

  color: #fff;
  content: "\D7";
  opacity: 0.6 !important;
  transition: all 0.3s;
}
.close:focus,
.close:hover {
  color: #fff !important;
  opacity: 1 !important;
}
button:hover[disabled] {
  cursor: not-allowed;
}
button.close[disabled] {
  opacity: 0.3 !important;
}

#approval {
  margin: 15px;
  margin-top: -40px;
  height: 100%;
}
#approval .pictureNavigation {
  height: 200px;
  background-color: #2b2b2b;
  padding: 10px;

  overflow: hidden;
  overflow-x: visible;
  overflow-y: hidden;

  white-space: nowrap;
}
#approval ul {
  white-space: nowrap;
  padding: 0px;
}
#approval ul li {
  display: inline-block;
}
#approval img.isSelected {
  border: 3px solid;
  border-color: #337ab7bf;
}
#approval img.preview {
  position: relative;
  float: left;
  margin-right: 5px;
  border-radius: 3px;
  max-width: 240px;
  max-height: 160px;
  transition: 0.3s border;
}
#approval .picPanel {
  margin-bottom: 10px;
  height: calc(100vh - 310px);
  text-align: center;
}
#approval img.selectedPic {
  border-radius: 3px;
  max-width: 100%;
  max-height: 100%;
  vertical-align: middle;
  transition: 0.3s all;
}
#approval .judgepanel {
  position: fixed;
  bottom: 0px;
  width: 100%;
  background-color: #191717;
  margin-left: -15px;
}
#approval .judgeButton {
  float: left;
  width: 50%;
  text-align: center;
  padding: 10px;
  cursor: pointer;
  transition: 0.3s background-color;
}
#approval .judgeButton:hover {
  background-color: #262626;
}
#approval .judgepanel .spacer {
  width: 2px;
  height: 100%;
  position: absolute;
  background-color: #272525;
  left: 50%;
}
.green {
  color: #5cba5e;
}
.red {
  color: #a94442;
}

/* Animations */
.fadeIn-enter-active {
  transition: opacity 0.3s;
}
.fadeIn-enter {
  opacity: 0;
}
.fadeIn-leave-to,
.fadeIn-leave-active {
  display: none;
  transition: none;
  opacity: 0;
}

.search_panel {
  max-width: 700px;
  margin: 0 auto;
  margin-top: -30px;

  padding: 0px 10px;
}
#foundItems {
  margin-top: 10px;
}
img.loading {
  background-color: #ffffff;
  padding: 10px;
  border-radius: 3px;
  z-index: 100;
  position: fixed;
  top: 10px;
  right: 10px;
  transition: all 0.3s;
}

#albumbrowser {
  padding: 10px;
}
#albumbrowser #albumselector {
  margin: -47px 5px 13px 5px;
  background-color: #191717;
  padding: 15px;
  border-radius: 3px;
}

.status_panel {
	margin: 0 auto;
	max-width: 700px;
}
.status_panel:after {
	content: "\F05A";
    font-family: FontAwesome;
    position: absolute;
    font-size: 800px;
    color: rgba(25, 25, 25, 0.34);
    top: -240px;
    left: -100px;
    z-index: 1;
}
.status_panel .batch {
	width: 200px;
    height: 100px;
    float: left;
    margin: 10px;
    padding: 10px;
    background-color: rgb(24, 23, 23);
    border-radius: 3px;
    transition: all 0.5s;
    border: 1px solid #1d1b1b;
    box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.63);
    transition: all 0.5s cubic-bezier(0.4, 0, 0.68, 0.97);
    z-index: 2;
    position: relative;
}
.status_panel .batch:hover {
	border: 1px solid rgba(75, 103, 121, 0.77);
    box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.63);
}
.status_panel .batch .title {
	font-weight: bold;
    font-size: 16px;
    color: #d4d4d4;
}

#upload {
	margin: 0 auto;
	max-width: 700px;
}
#upload .dropzone {
	background: #4c7ba2;
    font-weight: bold;
    font-size: 20px;
    border-radius: 10px;
    color: #f5f5f5;
    border-style: dashed;
    border-width: 3px;
}
#upload .dropzone .dz-preview .dz-image {
	overflow: inherit;
}

#settings_panel {
  max-width: 700px;
  margin: 0 auto;
  margin-top: -20px;

  padding: 0px 10px;
}
#settings_panel .panel {
  background-color: #191919;
  border-color: #191919;
}
#settings_panel .panel-default > .panel-heading {
  color: #fff;
  background-color: #131111;
  border-color: #000;
  font-weight: bold;
}
#settings_panel .alert-success {
  color: #f2fdf3;
  background-color: #7ca06d;
  border-color: #54654d;
}
#settings_panel .alert-danger {
  color: #f1fdf2;
  background-color: #9c5959;
  border-color: #5d2c2c;
}

#about {
  margin: 0 auto;
  max-width: 700px;
}
.about_panel {
  margin: -20px 10px;
  padding: 0px 10px;
  border: 1px solid #0e0d0d;
  border-radius: 6px;
  background: #191717;
}
.about_panel p {
  text-align: justify;
}
.about_panel h1,
#about_panel h2 {
  font-weight: bold;
}
